import PropTypes from "prop-types"
import React from "react"
import ParallaxCard from 'react-parallax-card'


const Workcard = ({ title, description, img, skills }) => (
  <div
    style={{
      display: "flex",
      flexDirection: "column",
      maxWidth: "18rem",
      borderRadius: 10,
      padding: "1rem",
      margin: "1rem",
    }}
  >
    <div style={{ boxShadow: "0px 0px 24px 0px rgba(0,0,0,0.2)", borderRadius: "5%" }}>
      <img src={img} alt={title} style={{ borderRadius: "5%" }}></img>
    </div>
    <h4 style={{ marginTop: "1rem", marginBottom: "1rem", justifyContent: 'center', }}>
      {title}
    </h4>
    <p>{description}</p>
    <div>

      {skills.map((skill, index) => {
        let url = "https://img.shields.io/badge/" + skill.skill + "-%20-" + skill.color
        return <img
          src={url}
          alt={skill.type}
          key={index}
          style={{ marginRight: "0.5rem", }}
        />
      })}
    </div>
  </div>
)

Workcard.propTypes = {
  siteTitle: PropTypes.string,
}

Workcard.defaultProps = {
  siteTitle: ``,
}

export default Workcard
