import PropTypes from "prop-types"
import React from "react"
import { useMediaQuery } from 'react-responsive'


const Banner = ({ siteTitle, siteDescription }) => (


  useMediaQuery({ query: '(min-device-width: 1224px)' }) ? < div
    style={{
      margin: `0 auto`,
      maxWidth: 960,
      padding: `1.45rem 1.0875rem`,
      display: "flex",
      alignItems: "center",
      flexDirection: "column",
      paddingTop: "10rem",

    }}
  >
    <h1 style={{ justifyContent: 'center', }}>
      {siteTitle}
    </h1>
    <p style={{ paddingTop: "1.5rem", fontSize: "1.5rem", lineHeight: "2.4rem", color: "#4e4e50", textAlign: "center" }}>{siteDescription}</p>
  </div > :
    < div
      style={{
        margin: `0 auto`,
        maxWidth: 960,
        padding: `1.45rem 1.0875rem`,
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        paddingTop: "3rem",

      }}
    >
      <h1 style={{ justifyContent: 'center', fontSize: "1.5em" }}>
        {siteTitle}
      </h1>
      <p style={{ paddingTop: "1rem", fontSize: "1.2rem", lineHeight: "2.4rem", color: "#4e4e50", textAlign: "center" }}>{siteDescription}</p>
    </div >
)

Banner.propTypes = {
  siteTitle: PropTypes.string,
}

Banner.defaultProps = {
  siteTitle: ``,
}

export default Banner
